import * as React from 'react';
import type { MobileZhiFactory } from '@ali/4ever-cangjie';
import { getData } from '@ali/4ever-utils';
import moFactory from '../mo/index';
import lazyRenderStrategy from '../common/queries/lazyRenderStrategy';
import SimpleCode from '../components/SimpleCode';
import CodeModel from '../mo/models';

export const mobileZhiFactory: MobileZhiFactory = (configs) => {
  return {
    ...moFactory({}),
    queries: {
      lazyRenderStrategy,
    },
    renderNode(props, _, next) {
      const { node, attributes } = props;
      const { type, text, data } = node as CodeModel;
      if (type !== 'code') {
        return next();
      }

      const { print = false } = configs.code || {};

      return (
        <SimpleCode
          node={node as CodeModel}
          {...attributes}
          code={text}
          theme={getData(data, 'theme', 'default')}
          height={!print ? getData(data, 'height', undefined) : undefined}
          lineWrapping={!!getData(data, 'wrap', false) || print}
          print={print}
        />
      );
    },
  }
}
